export default {
	data() {
		return {
			listinfo: {},
			userinfo:{},
			id:"",
			finished:false
		}
	},
	
	onShow() {
		var me = this;
		var userinfo = me.getGlobalUser("userInfos");
		if(userinfo != null){
			me.userinfo = userinfo;
			me.logined = userinfo.logined;
			
		}else{
			me.logined = false;
			me.userinfo = {};
		}
	},
	
	onLoad(params) {
		var me = this;
		var id = params.id;
		this.id = id;
		
		uni.request({
			url:this.mainUrl+'order/order/findOrderInfoById',
			method:"GET",
			data:{
				order_id:id
			},
			success:(res) => {
				console.log(res);
				var listinfo = res.data.data;
				this.listinfo = listinfo;
				console.log("listinfo:");
				console.log(this.listinfo);
			}
		})
	},
	
	methods: {
	},
	
	components: {
	}
}
